.reg-header label {
    margin-right: 4px;
  }
  
  .registration-form{
      border:1px solid black;
      padding:40px;
      max-width: fit-content;
      margin:auto;
      border-radius:10px;
      text-align: end;
      background-color: white;
      box-shadow: 5px 5px 3px grey;
      margin-top:20vh;
      position: relative;
  }
  .registration-button{
      border:.5px solid black;
      background-color: white;
      border-radius: 10px;
      margin-bottom:8px;
      padding-top:2px;
      padding-bottom: 2px;
  }
  
  .registration-button:hover{
      background-color: rgb(4, 168, 4);
      color:white;
      cursor: pointer;
  }
  
  .login-redirect:hover{
      border-bottom: 1px solid blue;
      color:blue;
      cursor: pointer;
  } 
  
  .auth-error {
    color: red;
    display: inline-block;
    width: 90%;
  }
  
  .auth-error-container {
    position: absolute;
    top: -60px;
    right: 0;
    left: 0;
    text-align: center;
    margin: auto;
    color: red;
  }
  
  footer{
      margin-top:10px;
       padding:5px;
  }
  
  footer a{
      font-size:small;
  }
  @media only screen and (max-width: 760px) {
  
      .registration-form{
          padding:20px;
      }
      .registration-button{
          background-color: rgb(4, 168, 4);
          color:white;
     
      }
      /* .auth-error-container {
        bottom: -100px;
      } */
    
  }