.main-header {
    position: fixed;
    width: 100%;
    background-color:rgba(171, 179, 179, 0.411);
    height:40px;
    z-index: 100;
    top: 0;
    padding: 3px;
    box-shadow: 0px 3px 15px rgba(0,0,0,0.2);
    opacity: .95;
  }
  
  .main-header nav {
    padding: 7px 4%;
  }
  
  .nav-links nav {
    padding-right: 0;
    padding-left: 0;
    position: relative;
    bottom: 2px;
  }
  
  .main-header h1 {
    float: left;
    margin: 0;
    font-size: 16px;
  }
  
  .nav-links {
    display: block;
    float: right;
    font-size: 14px;
    position: relative;
    bottom: 6px;
    width: 300px;
    text-align: right;
  }
  
  .nav-links a {
    margin-left: 1em;
  }
  
  .nav-links p {
    display: inline;
    cursor: default;
  }
  
  .nav-links a:hover {
    opacity: 0.7;
  }
  
  .deal {
    color:  rgb(195, 0, 255)
  }
  .meal{
    color: rgb(64, 110, 3)
  }
  
  @media only screen and (max-width: 760px) {
    .nav-links {
      width: 200px;
    }
    .nav-links a, .nav-links p {
      font-size: .85em;
    }
  
    .main-header nav {
      padding: 7px 2%;
    }
  
    .nav-links nav{
      display: inline-block;
      padding-right: 0;
      padding-left: 0;
      width: 160px;
    }
  }
  