.log-reg-header {
    margin: 0;
    text-align: left;
  }
  
  .login-input label {
    margin-right: 4px;
  }
  
  .log-in-form{
      border:1px solid black;
      padding:40px;
      max-width: fit-content;
      margin:auto;
      border-radius:10px;
      text-align: end;
      background-color: white;
      box-shadow: 5px 5px 3px grey;
      margin-top:20vh;
      position: relative;
  }
  .log-in-button{
      margin-bottom:8px;
      border:.5px solid black;
      border-radius: 10px;
      padding-top:2px;
      padding-bottom: 2px;
      background-color: white;
  }
  
  .log-in-button:hover{
      background-color: rgb(4, 168, 4);
      color:white;
      cursor: pointer;
  }
  
  .login-redirect :hover{
      border-bottom: 1px solid blue;
      color:blue;
      cursor: pointer;
  }
  
  
  footer{
      margin-top:10px;
      padding:5px;
  }
  
  footer a{
      font-size:small;
     
  } 
  
  .login-error {
    color: red;
  }
  
  .log-error-container {
    position: absolute;
    top: -60px;
    right: 0;
    left: 0;
    text-align: center;
    margin: auto;
    color: red;
  }  